
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  margin-top: 60px;
  -webkit-animation:begin 1s;
          animation:begin 1s;
  position:relative;
}
@-webkit-keyframes begin{
from{opacity:0;top:15vh;
}
to{opacity:1;top:0;
}
}
@keyframes begin{
from{opacity:0;top:15vh;
}
to{opacity:1;top:0;
}
}



